<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 教师管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container" v-loading="loading"> 
            <div class="form">
                <div class="form-top">
                    <el-button class="fr downloadbutton" type="primary" size="small">下载用户模板</el-button>
                    <el-button class="fr" type="primary" size="small">导入教师用户</el-button>
                    <el-button class="fr" type="primary" @click="editVisible=true" size="small">创建教师用户</el-button>
                </div>
                <div class="cont">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="number"
                        label="学号">
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="姓名">
                      </el-table-column>
                      <el-table-column
                        prop="academyName"
                        label="院系">
                      </el-table-column>
                      <el-table-column label="操作" width='240px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="handleEdit(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="info"
                              @click="resetpassword(scope.row)">重置密码</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delworks(scope.row.id)">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                        <el-pagination
                          background
                          :page-size="pageSize"
                          @current-change="changePage"
                          layout="prev, pager, next"
                          :total="total">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
        <!-- 新建弹出框 -->
        <el-dialog title="创建老师" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="number" size="small" placeholder="请输入老师编号"></el-input>
            <el-input class="margin10" v-model="name" size="small" placeholder="请输入老师姓名"></el-input>
            <el-input class="margin10" v-model="academyId" size="small" placeholder="请输入学院ID"></el-input>
            <el-button type="primary" @click="Addteacher" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑老师" class="datas-bg" :visible.sync="editVisible2" width="380px">
            <el-input class="margin10" v-model="editItems.number" size="small" placeholder="请输入老师编号"></el-input>
            <el-input class="margin10" v-model="editItems.name" size="small" placeholder="请输入老师姓名"></el-input>
            <el-input class="margin10" v-model="editItems.academyId" size="small" placeholder="请输入学院ID"></el-input>
            <el-button type="primary" @click="modifyteacher(1)" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
    </div>
</template>

<script>
import {fetchAddteacher, fetchListteacher, fetchUpdateteacher, fetchDeleteteacher} from "../../axios/requests";
export default {
    name: 'course',
    data() {
        return {
            pageNum:1,
            pageSize:10,
            total:0,

            editItems:'',

            loading:false,
            academyId:'',
            name:'',
            number:'',
            editVisible:false,
            editVisible2:false,
            choosed:'未完成课程设计教学',
            items:[1,1,1],
            tableData: [],
        }; 
    },
    mounted(){
        this.Listteacher()
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(e){
            this.editVisible2=true;
            this.editItems = Object.assign({}, e);
        },
        resetpassword(e){
          this.editItems = Object.assign({}, e);
          this.$confirm('确定要重置密码吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.modifyteacher(2)
            })
            .catch(() => {});
        },
        delworks(e){
            this.$confirm('确定要删除此教师吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deleteteacher(e)
            })
            .catch(() => {});
        },
        changePage(page){
          this.pageNum = page;
          this.Listteacher();
        },
        async Addteacher() {//添加老师
          let {number, name, academyId} = this;
            if (!number) {this.$message.error('编号不能为空');return};
            if (!name) {this.$message.error('姓名不能为空');return};
            if (!academyId) {this.$message.error('学院ID不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchAddteacher(number, name, academyId);
              if (response.status.code ==0) {
                this.$message({
                  message: '添加成功',
                  type: 'success'
                });
                this.editVisible = false;
                this.Listteacher()
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Listteacher() {//得到老师列表
            this.loading = true;
            let {pageNum, pageSize} = this;
            try {
              let response = await fetchListteacher(pageNum, pageSize);
              if (response.status.code ==0) {
                this.total = response.result.total;
                this.tableData = response.result.list;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async modifyteacher(type) {//修改老师信息
          let editItems = this.editItems;
            if (!editItems.number) {this.$message.error('编号不能为空');return};
            if (!editItems.name) {this.$message.error('姓名不能为空');return};
            if (!editItems.academyId) {this.$message.error('学院ID不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = type==1?await fetchUpdateteacher(editItems.id, editItems.academyId, editItems.number, editItems.name):await fetchUpdateteacher(editItems.id, editItems.academyId, editItems.number, editItems.name, 123456);
              if (response.status.code == 0) {
                if (type==1) {
                  this.$message({
                    message: '修改成功',
                    type: 'success'
                  });
                  this.editVisible2 = false;
                }else{
                  this.$message({
                    message: '重置成功，密码123456',
                    type: 'success'
                  });
                }
                this.Listteacher();
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Deleteteacher(id) {//删除老师
            const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchDeleteteacher(id);
              if (response.status.code ==0) {
                this.$message({
                  message: '删除成功',
                  type: 'success'
                });
                this.Listteacher();
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .downloadbutton{
      margin-left:10px;
    }
    .margin10{
      margin-bottom:10px;
      width:100%;
    }
</style>


